import React, { useState } from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle.js";
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {NavLink} from "react-router-dom";
import Button from '@material-ui/core/Button';
import SearchIcon from '@material-ui/icons/Search';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  // NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from 'reactstrap';

const Dnavbar = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (<>
   
      <Navbar color="" light expand="xl" style={{backgroundColor:'#2D2926FF'}}>
       
         
             <img src="https://i.ibb.co/KDZt8pH/logo.png" style={{width:'100px',height: '60px'}}/>
              <NavbarBrand href="/" style={{color:'white'}}>Depressor</NavbarBrand>
              <NavbarToggler onClick={toggle} style={{backgroundColor:'white'}}/>
               <Collapse isOpen={isOpen} navbar>
        
                    <Nav className="ml-auto" navbar>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/"><input type="search" placeholder="Search" id="nav_search"/><Button style={{backgroundColor:'#570029'}} id="text_white"><SearchIcon/></Button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/profile"><button className="btn btn-danger">Profile</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/chat"><button className="btn btn-danger">Chat</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/selfhealing"><button className="btn btn-danger">SelfHealing</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/reliever"><button className="btn btn-danger">Reliever</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/contribution"><button className="btn btn-danger">Contribution</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/story"><button className="btn btn-danger">Story</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/buy"><button className="btn btn-danger">Buy</button></NavLink></NavItem>
                      <NavItem><NavLink href="#" id="navbar_tab" to="/cart"><button className="btn"><ShoppingCartIcon style={{color:'#E94B3CFF'}}/></button></NavLink></NavItem>
                      <UncontrolledDropdown nav inNavbar>
                        <DropdownToggle nav caret>
                          <AccountCircleIcon style={{color:'#E94B3CFF'}}/>
                        </DropdownToggle>
                        <DropdownMenu right>
                            <DropdownItem>harryoswal333</DropdownItem><DropdownItem divider />
                           <DropdownItem>  Logout   </DropdownItem>     
                        </DropdownMenu>
                      </UncontrolledDropdown>
                    </Nav>       
                </Collapse>       
       
      </Navbar>
  


    </>
    );
}
export default Dnavbar;
