import React,{useState} from 'react';
import Dnavbar from "./Dnavbar";
import MenuBookIcon from '@material-ui/icons/MenuBook';
import AdjustIcon from '@material-ui/icons/Adjust';
import AccessibilityNewIcon from '@material-ui/icons/AccessibilityNew';
import SpaIcon from '@material-ui/icons/Spa';
import RemoveIcon from '@material-ui/icons/Remove';
import FreeBreakfastIcon from '@material-ui/icons/FreeBreakfast';
import NotificationsIcon from '@material-ui/icons/Notifications';
import Button from '@material-ui/core/Button';
import CategoryIcon from '@material-ui/icons/Category';
import ForumIcon from '@material-ui/icons/Forum';
import Footer from "./Footer";
import Ddatameditation from "./Ddatameditation";
import Ddatachitshakti from "./Ddatachitshakti";
import Ddatameditationbenefits from "./Ddatameditationbenefits";
import Ddatamedscience from "./Ddatamedscience";
import Ddatayogawhat from "./Ddatayogawhat";
import Ddatayogahistory from "./Ddatayogahistory";
import Ddatayogatypes from "./Ddatayogatypes";
import Ddatayogabenifits from "./Ddatayogabenifits";
import Ddatayogamyths from "./Ddatayogamyths";
import Ddataspirit from "./Ddataspirit";
import Ddatagoodhabit from "./Ddatagoodhabit";
import Ddatabadhabit from "./Ddatabadhabit";


const Dselfhealing=()=>{

	const [toggler,settoggler]=useState(2);
	const [meditate,setmeditate]=useState(1);
	const [yoga,setyoga]=useState(1);
	const [div,setdiv]=useState("meditate");

	const changediv=(id)=>{
		console.log(id);
		if(id=="chit"){
			setdiv("chit");

		}
		if(id=="medbenefits"){
			setdiv("medbenefits");
		}
		if(id=="meditate"){
			setdiv("meditate");
		}
		if(id=="medscience"){
			setdiv("medscience");
		}
		if(id=="yogawhat"){
			setdiv("yogawhat");
		}
		if(id=="yogahist"){
			setdiv("yogahist");
		}
		if(id=="yogatype"){
			setdiv("yogatype");
		}
		if(id=="yogabenefit"){
			setdiv("yogabenefit");
		}
		if(id=="yogamyth"){
			setdiv("yogamyth");
		}
		if(id=="goodhabit"){
			setdiv("goodhabit")
		}
		if(id=="spirit"){
			setdiv("spirit")
		}
		if(id=="badhabit"){
			setdiv("badhabit")
		}
		

	}
	return(<>
			<Dnavbar/>
			<Button onClick={()=>settoggler(toggler+1)} style={{color:'white'}}><MenuBookIcon/>Self Healing</Button>
				<div >
				{toggler%2==0?
					<div style={{float:'left',color:'white',marginLeft:'2px',width:'300px'}}>
						<span id="text_white" style={{marginLeft:'10px'}}>Kits<hr style={{width:'200px',height:'2px'}}/></span><br/>
						<span id="text_white"><Button id="text_white" onClick={()=>setmeditate(meditate+1)}><AdjustIcon id="text_white"/>Meditation Kit</Button><hr style={{width:'200px',height:'2px'}}/></span>
							{meditate%2==0?<ul style={{listStyleType:'none'}}>
								<li><Button id="text_white"onClick={e=>changediv("chit")}><CategoryIcon id="text_white" />Guided Meditations – Isha Kriya and Chit Shakti</Button>
								 
								</li><hr style={{width:'200px',height:'2px'}}/>
								<li><Button id="text_white" onClick={e=>changediv("medbenefits")}><CategoryIcon id="text_white"/>Five Amazing Benefits of Meditation</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>
								<li><Button id="text_white"onClick={e=>changediv("meditate")}><CategoryIcon id="text_white"/>How to Meditate</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>
								<li><Button id="text_white" onClick={e=>changediv("medscience")}><CategoryIcon id="text_white"/>Science of Meditation</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>

							



							</ul>:null
						}
						<span id="text_white"><Button id="text_white"onClick={()=>setyoga(yoga+1)}><AccessibilityNewIcon/>Yoga Kit</Button><hr style={{width:'200px',height:'2px'}}/></span>
									{yoga%2==0?<ul style={{listStyleType:'none'}}>
								<li><Button id="text_white"onClick={e=>changediv("yogawhat")}><CategoryIcon id="text_white" />What is Yoga?</Button>
								 
								</li><hr style={{width:'200px',height:'2px'}}/>
								<li><Button id="text_white" onClick={e=>changediv("yogahist")}><CategoryIcon id="text_white"/>History of Yoga</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>
								<li><Button id="text_white"onClick={e=>changediv("yogatype")}><CategoryIcon id="text_white"/>Types of Yoga</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>
								<li><Button id="text_white" onClick={e=>changediv("yogabenefit")}><CategoryIcon id="text_white"/>Benefits of Yoga</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>
								  <li><Button id="text_white" onClick={e=>changediv("yogamyth")}><CategoryIcon id="text_white"/>Myths of Yoga</Button>
									
								  </li><hr style={{width:'200px',height:'2px'}}/>
								 

							



							</ul>:null
						}	
						<span id="text_white"><Button id="text_white" onClick={e=>changediv("spirit")}><SpaIcon/>Spiritual Kit</Button><hr style={{width:'200px',height:'2px'}}/></span>
							
						<span id="text_white"><Button id="text_white" onClick={e=>changediv("badhabit")}><RemoveIcon/>Good bye bad habits</Button><hr style={{width:'200px',height:'2px'}}/></span>
						<span id="text_white"><Button id="text_white" onClick={e=>changediv("goodhabit")}><FreeBreakfastIcon/>Incorporate Good Habits</Button><hr style={{width:'200px',height:'2px'}}/></span>
							
						<span id="text_white"><Button id="text_white"><ForumIcon/>Members Visited:0</Button><hr style={{width:'200px',height:'2px'}}/></span>
						<span id="text_white"><Button id="text_white"><ForumIcon/>Updates:0</Button><hr style={{width:'200px',height:'2px'}}/></span>
						<span id="text_white"><Button id="text_white"><NotificationsIcon/>Notifications:0</Button><hr style={{width:'200px',height:'2px'}}/></span>
							
				</div>:null
				}
				<div id="selfhealdatadiv">
					<div className="container">
					{div=="meditate"?<Ddatameditation/>:div=="chit"?<Ddatachitshakti/>:div=="medbenefits"?<Ddatameditationbenefits/>:div=="medscience"?
					<Ddatamedscience/>:div=="yogawhat"?<Ddatayogawhat/>:div=="yogahist"?<Ddatayogahistory/>:
					div=="yogabenefit"?<Ddatayogabenifits/>:div=="yogamyth"?<Ddatayogamyths/>:div=="yogatype"?<Ddatayogatypes/>:
					div=="spirit"?<Ddataspirit/>:div=="goodhabit"?<Ddatagoodhabit/>:div=="badhabit"?<Ddatabadhabit/>:null}
					</div>

				
				
					
				
				</div>
				

				</div>
			
				<Footer/>


			
		</>)
}

export default Dselfhealing;