import React from 'react';
import Dnavbar from "./Dnavbar";
import Button from '@material-ui/core/Button';
import Footer from "./Footer"

const Dprofile=()=>{

	const validation=()=>{
		var f_name=document.getElementById('fname').value;
		var d_ob=document.getElementById('dob').value;
		if(f_name==""){alert('Please enter the family name.')}
		else if(d_ob==""){alert('please enter the dob!!')}
		else{alert(`Hi ${f_name} You can now proceed to chat section.And your DOB is:${d_ob}`)}
	}
	return(<>
			<Dnavbar/>
			<div className="container">
				<div className="row">
					<div className="col-xl-3 col-lg-3 col-md-3 col-sm-12 col-12">

					</div>
					<div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-12" id="formshadowdiv">
						<h1 id="text_pink">Let Us Create Profile</h1><br/>
						
						<div class="image-upload">
  							<label for="file-input">
    							<img src="https://i.ibb.co/5GPfj9r/fg.png"/>
  							</label>

  							<input id="file-input" type="file" />
						</div>
						<span id="text_white" style={{float:'left'}}>Give Your Family Name</span><br/>
						<input class="letusinput" id="fname" type="text" placeholder="Family Name"/><br/>
						<span id="text_white"style={{float:'left'}}>Date of Birth</span><br/>
						<input class="letusinput" id="dob" type="text" placeholder="dd-mm-yyyy"/><br/>
						<span id="text_white"style={{float:'left'}}>Depressor/Reliever</span><br/>
						<select id="letus_select">
							<option  value="Depressor">Depressor</option>
							<option value="Depressor">Reliever</option>
							<option value="Depressor">Both</option>
						</select><br/>
						<span id="text_white"style={{float:'left'}}>What are you doing</span><br/>
						<select id="letus_select">
							<option value="School Student">School Student</option>
							<option value="College Student">College Student</option>
							<option value="Doing Job">Doing Job</option>
							<option value="Graduate but no Job">Graduate but no Job</option>
							<option value="Settled Person with job and wife and kids">Settled Person with job and wife and kids</option>

						</select><br/>
						<span id="text_white"style={{float:'left'}}>Your Hobbies</span><br/>
						<select id="letus_select">
							<option value="Reading">Reading</option>
							<option value="Writing">Writing</option>
							<option value="Dancing">Dancing</option>
							<option value="Sports">Sports</option>
							<option value="Spirituality">Spirituality</option>
							<option value="Yoga">Yoga</option>
							<option value="Singing and playing Music">Singing and playing Music</option>
							<option value="Movies/Documentory">Movies/Documentory</option>

						</select><br/>

						<Button style={{color:'white',backgroundColor:'skyblue',marginTop:'30px',float:'right'}} onClick={validation}>Go</Button>

					</div>
					<div className="col-xl-3 col-lg-3 col-md-3 col-sm-12 col-12">

					</div>
				</div>
			</div>
			<Footer/>
			
			
		</>)
}

export default Dprofile;
