import React,{useState} from 'react';
import Dnavbar from "./Dnavbar";
import Button from '@material-ui/core/Button';
import {  Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import Dstoryphoto from "./Dstoryphoto";
import Dcontributedata from "./Dcontributedata";
import Dcardstory from "./Dcardstory";
import Footer from "./Footer";

const Dstory=()=>{

	const changephoto=(id,imga)=>{
		setphotoid(id);
		setstoryphoto(imga);
		console.log(photoid);
		

	}
	const[storyphoto,setstoryphoto]=useState();
	const [modal, setModal] = useState(false);
	const [photoid,setphotoid]=useState(3);
	const toggle = () => setModal(!modal);
	const [expand,setexpand]=useState(false);
	const [additem,setadditem]=useState([]);
	const [story,setstory]=useState({
		storysec:'',
		emailsec:'',
		image:'',
	});

	const inputevent=(event)=>{
		const {name,value}=event.target;
		setstory((olddata)=>{
			return{
				...olddata,
				[name]:value,
				image:storyphoto,
			};
		});

		console.log(story);
	}


	const expandsidediv=()=>{
		setexpand(!expand);
	}

	const changeinput=(event)=>{
		console.log(event.target.value)
	}

	const addstory=()=>{
		var story_data=document.getElementById('Dcomment').value;
		var email_data=document.getElementById('email').value;
		if(story_data==""){alert('Story Field cannot be empty!!!')}
		else if(email_data==""){alert('Enter the email.')}
		else{

		setadditem((olddata)=>{
			return([...olddata,story])
		});
	}
		

	}
	const checkboxes=(event)=>{
		console.log(event.target.value)
	}
	
	return(<>
			<Dnavbar/>
			<div className="container" id="text_white">
				<div className="row">
					<div className="col-xl-8 col-lg-8 col-md-12 col-sm-12 col-12">
						<div className="container">
							<div className="row">
								<Dcardstory story="I was depressed for a long time.But support given by 
								relievers is awesome.Relievers even called me twice a week just to show support and love.
								I gone through anxiety as well.But the support here is caring.All the relievers wants 
								to cure their depressors.Thanks to reliever Vyomesh!!!" email="harryoswal333@gmail.com"
								imgr="https://miro.medium.com/max/600/1*HrUvM05Q-8RxFsVj1wj4Vg.jpeg"/>
								<Dcardstory story="I was depressed for a long time.But support given by 
								relievers is awesome.Relievers even called me twice a week just to show support and love.
								.Thanks to reliever Vyomesh!!!" email="harryoswal333@gmail.com"
								imgr="https://media.gettyimages.com/photos/portrait-of-happy-male-tourist-sitting-on-retaining-wall-of-bridge-picture-id605380757?s=170667a"/>
								<Dcardstory story="I was depressed for a long time.But support given by 
								relievers is awesome.Relievers even called me twice a week just to show support and love.
								.Thanks to reliever Vyomesh!!!" email="harryoswal333@gmail.com"
								imgr="https://media.gettyimages.com/photos/man-winking-to-a-camera-and-taking-a-selfie-picture-id886840712?s=612x612"/>
									<Dcardstory story="I was depressed for a long time.But support given by 
								relievers is awesome.Relievers even called me twice a week just to show support and love.
								.Thanks to reliever Vyomesh!!!" email="harryoswal333@gmail.com"
								imgr="https://media.gettyimages.com/photos/selfie-time-picture-id1170911876?s=612x612"/>
									<Dcardstory story="I was depressed for a long time.But support given by 
								relievers is awesome.Relievers even called me twice a week just to show support and love.
								.Thanks to reliever Vyomesh!!!" email="harryoswal333@gmail.com"
								imgr="https://media.gettyimages.com/photos/portrait-of-cute-girl-picture-id939108006?s=612x612"/>
									<Dcardstory story="I was depressed for a long time.But support given by 
								relievers is awesome.Relievers even called me twice a week just to show support and love.
								.Thanks to reliever Vyomesh!!!" email="harryoswal333@gmail.com"
								imgr="https://c8.alamy.com/comp/R5WFDK/portrait-of-a-happy-teen-girl-with-eyes-closed-golden-hour-R5WFDK.jpg"/>
									{additem.map((cvalue,index)=>{
										return(<Dcardstory imgr={cvalue.image} story={cvalue.storysec} email={cvalue.emailsec}/>);
									})}

							
							

							</div>
						</div>
					
						
					</div>
					<div className="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12">
						<div >
							<textArea style={{marginTop:'10px',marginLeft:'15px',width:'100%'}} id="Dcomment"  type="text" placeholder="Write Your story" name="storysec" value={story.storysec} onChange={inputevent} onClick={expandsidediv}/>
							{expand==true?<div style={{transition:'0.3s'}}>
							<hr/>
							
							<span>Choose your Favourite Reliever</span><br/>
							<Button style={{backgroundColor:'#FFB6C1'}} id="text_pink"onChange={inputevent} onClick={toggle}>Choose</Button>
							{Dcontributedata.map((cvalue,index)=>{
								if(index==photoid){return(<Dstoryphoto id={cvalue.id} outshow={changephoto} img={cvalue.img} />);}
							})}
						<hr/>
						<span>Email Id</span><br/>
							<input style={{width:'100%'}} id="email"  type="email" placeholder="lilylily3@gmail.com" name="emailsec" value={story.emailsec} onChange={inputevent}/><hr/>
							<span>How this website is different than others</span><br/>
							<textArea style={{width:'100%'}} id="Dcomment"  type="text" placeholder="write"/><hr/>
							<span>Problems that you had</span><br/>
							<div className="container">
							<label for="depression" style={{width:'100px'}} >Depression</label>
							<input  type="checkbox" name="depression" onChange={checkboxes} /><br/>
							<label for="anxiety" style={{width:'100px'}}>Anxiety</label>
							<input  type="checkbox" name="anxiety"/><br/>
							<label for="stress" style={{width:'100px'}}>Stress</label>
							<input type="checkbox" name="stress"/><br/>
							<label for="tension" style={{width:'100px'}}>Tension</label>
							<input type="checkbox" name="tension"/><br/>
							<label for="sucide" style={{width:'100px'}}>Suicidial</label>
							<input type="checkbox" name="sucide"/>
							</div>
							<Button id="text_pink" style={{backgroundColor:'pink',float:'right'}} onClick={addstory}>Submit</Button>
							</div>:null}



						</div>
					</div>
				</div>
			</div>
			 <div>
    
      <Modal isOpen={modal} toggle={toggle} className="modal-dialog modal-dialog-centered">
        <ModalHeader toggle={toggle}>Choose Relievers</ModalHeader>
        <ModalBody>
         Relievers:
         	{Dcontributedata.map((cvalue)=>{
							return(<Dstoryphoto id={cvalue.id} choose="choose" name={cvalue.name} img={cvalue.img} outshow={changephoto}/>);
						})}
        </ModalBody>
        <ModalFooter>
        
        
        </ModalFooter>
      </Modal>
 	</div>
 	<Footer/>
		</>)
}

export default Dstory;