import React from 'react';
import Lidata from "./Lidata";
import Li from "./Li"
import Dcard from "./Dcard";
import Button from '@material-ui/core/Button';
import Dcards from "./Dcards";
const image1="https://i.ibb.co/HVZm1PB/Capture5.jpg";
const Dlive=()=>{
	return(<>
		<div className="container" style={{marginTop:'9%'}}>
			<div className="row">
				<div className="col-xl-6 col-md-6 col-lg-6 col-sm-12 col-12">
					<h1 style={{color:'#e32160'}}>How you can live your Life?</h1>
					<p id="text_white">
						<ul>
							{Lidata.map((cvalue)=>{
								return(<Li text={cvalue.item}/>)
							})}
						</ul>
					</p>

				</div>
				
				 <Dcard heading="Just Feel Everything" descript="Make your family" img={image1}/>
				
			</div>
		</div>
		<div className="container" style={{marginTop:'9%'}}>
			<div className="row">
				<div className="col-xl-8 col-md-8 col-lg-8 col-sm-12 col-12">
					<h1 style={{color:'#e32160'}}>What We can provide?</h1>
					<p id="text_white" style={{textAlign:'justify'}}>We can provide you the best approach to solve any kind of problem related to mental,physical.
					We designed this website to help those people who are suffering from
					depression,anxiety,stress,tensions. We have a large number of people who are relievers as well as your family.
					The people who joined us are of two types:Depressor and relievers.Depressors are the people who are suffering from these
					kind of depressions and sucidial thoughts.On the other hand Relievers are the people who cure your
					depression and suicidial thoughts.We provide you the different sections that helps you openup yourself such as:
							
					</p>

				</div>
				<div className="col-xl-4 col-md-4 col-lg-4 col-sm-12 col-12">
					<div className="container">
						<div className="row">
							<div className="col-xl-6 col-md-6 col-lg-6 col-sm-6 col-6">
								<p id="text_white" style={{marginLeft:'14%'}}>Family Members:<br/>Depressors:
								<br/>Relievers:</p>

							</div>
							<div className="col-xl-6 col-md-6 col-lg-6 col-sm-6 col-6">
								<span id="text_pink">0</span><br/>
								<span id="text_pink">0</span><br/>
								<span id="text_pink">0</span><br/>
								
							</div>
						</div>
					</div>
					
					<span id="text_white">Comment</span><br/>
				 	<textarea id="Dcomment" type="text" placeholder="Ask us..."/><br/><Button id="Dsendbut">Send</Button>
				</div>
			</div>
		</div>
		<div className="container" style={{marginTop:'9%'}}>
			<div className="row">
		
				{Dcards.map((cvalue)=>{
					return( <Dcard heading={cvalue.heading} descript={cvalue.descript} img={cvalue.img}/>);
				})}
					
			
			</div>
		</div>

		
		</>);

}

export default Dlive;
