import React,{useState} from 'react';

const Dstoryphoto=(props)=>{

	const [expand,setexpand]=useState(false);
	const call=()=>{
		
		setexpand(!expand);
	}

	const callstorychangephoto=()=>{
		props.outshow(props.id,props.img)
	}
	return(<>
				<div><img onClick={call} id={expand==true?"contributephotoexpand":"contributephoto"} src={props.img}/>
				<b>{props.name}</b><br/>
				<a href="#" style={{float:'right',textDecoration:'none'}} onClick={callstorychangephoto}>{props.choose}</a>
				{props.text}
				</div><hr/>
		</>)
}

export default Dstoryphoto;